html,
body {
  width: 100vw;
  height: 100vh;
  margin: 0;
}
body {
  font-family: sans-serif;
  color: #fff;
  background: #1edad0;
}
.text-3d {
  font-size: 6rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) skewY(0deg);
  transform-origin: bottom left;
  color: transparent;
  text-shadow: 1px -1px 10px rgba(255, 255, 255, 0.4),
    0px -1px 10px rgba(255, 255, 255, 0.4),
    -1px -1px 10px rgba(255, 255, 255, 0.4), 0px -2px 3px #fff,
    -1px -2px 3px #fff, -2px -2px 3px #fff, -1px -3px 2px #ddd,
    -2px -3px 2px #ddd, -3px -3px 2px #ddd, -2px -4px 2px #fff,
    -3px -4px 2px #fff, -4px -4px 2px #fff, -3px -5px 1px #fff,
    -4px -5px 1px #fff, -5px -5px 1px #fff;
}
.text-3d:before {
  content: "React 3D Text";
  position: absolute;
  left: 0;
  top: 0;
  transform: skewX(30deg);
  transform-origin: bottom left;
  z-index: -1;
  color: transparent;
  text-shadow: 15px 0px 20px rgba(0, 0, 0, 0.5),
    15px 0px 25px rgba(0, 0, 0, 0.5), 15px 0px 35px rgba(0, 0, 0, 0.5),
    15px 0px 45px rgba(0, 0, 0, 0.5);
}
